* {
    box-sizing: border-box; }
$color: #fff;
button {
    width: 250px;
    height: 50px;
    background-color: #ffe66b;
    border: none;
    cursor: pointer;
    outline: none;
    transition: 0.5s 0s;
    &:hover {
        background-color: pink;
        color: #fff; }
    &:active {
        background-color: orange; }
    &:focus {
        background-color: grey; } }

.circle {
    width: 50px;
    height: 50px;
    background-color: green;
    border-radius: 100%;
    margin-top: 10px;
    transition: 0.5s 0s;
    position: relative;
    &::before {
            content: '';
            display: block;
            top: 50%;
            left: 20%;
            position: absolute;
            height: 4px;
            width: 30px;
            background-color: black;
            transform: translateY(-50%);
            transition: 1s all; }
    &::after {
            content: '';
            display: block;
            transform: translateX(-50%);
            top: 20%;
            left: 50%;
            position: absolute;
            height: 30px;
            width: 4px;
            background-color: black;
            transition: 1s all; }
    &:hover {
        &:before, &:after {
            background-color: $color; } } }


h1 {

    color: green;
    text-align: center; }

ul {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto;
    outline: 1px solid green;
    font-size: 20px;
    font-weight: bold;
    padding-inline-start: 0px;
    li {
        width: 180px;
        height: 180px;
        background-color: green;
        color: #cccccc;
        list-style: none;
        border-radius: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 1s all;
        &:hover {
            background-color: grey;
            color: #fff;
            transform: rotate(360deg); }
        &:nth-child(2n) {
            flex-direction: column;
            background-color: red; } } }




